import { $xhc } from '../tsServer/plugins';
import { Command } from './commandManager';

export class $hkc implements Command {
	public readonly id = '_typescript.configurePlugin';

	public constructor(
		private readonly a: $xhc,
	) { }

	public execute(pluginId: string, configuration: any) {
		this.a.setConfiguration(pluginId, configuration);
	}
}
