import * as vscode from 'vscode';
import { DocumentSelector } from '../configuration/documentSelector';
import { ClientCapability, ITypeScriptServiceClient } from '../typescriptService';
import DefinitionProviderBase from './definitionProviderBase';
import { $Sic, $Vic } from './util/dependentRegistration';

export default class $7jc extends DefinitionProviderBase implements vscode.TypeDefinitionProvider {
	public provideTypeDefinition(document: vscode.TextDocument, position: vscode.Position, token: vscode.CancellationToken): Promise<vscode.Definition | undefined> {
		return this.b('typeDefinition', document, position, token);
	}
}

export function $8jc(
	selector: DocumentSelector,
	client: ITypeScriptServiceClient,
) {
	return $Sic([
		$Vic(client, ClientCapability.EnhancedSyntax, ClientCapability.Semantic),
	], () => {
		return vscode.languages.registerTypeDefinitionProvider(selector.syntax,
			new $7jc(client));
	});
}
