import * as vscode from 'vscode';
import { DocumentSelector } from '../configuration/documentSelector';
import { LanguageDescription } from '../configuration/languageDescription';
import { API } from '../tsServer/api';
import type * as Proto from '../tsServer/protocol/protocol';
import { Position } from '../typeConverters';
import { ClientCapability, ITypeScriptServiceClient } from '../typescriptService';
import { $whc } from '../utils/dispose';
import $Kic, { $Lic, $Mic } from './fileConfigurationManager';
import { $Sic, $Tic, $Vic } from './util/dependentRegistration';


const inlayHintSettingNames = Object.freeze([
	$Lic.parameterNamesSuppressWhenArgumentMatchesName,
	$Lic.parameterNamesEnabled,
	$Lic.variableTypesEnabled,
	$Lic.variableTypesSuppressWhenTypeMatchesName,
	$Lic.propertyDeclarationTypesEnabled,
	$Lic.functionLikeReturnTypesEnabled,
	$Lic.enumMemberValuesEnabled,
]);

class TypeScriptInlayHintsProvider extends $whc implements vscode.InlayHintsProvider {

	public static readonly minVersion = API.v440;

	private readonly a = new vscode.EventEmitter<void>();
	public readonly onDidChangeInlayHints = this.a.event;

	constructor(
		private readonly b: LanguageDescription,
		private readonly f: ITypeScriptServiceClient,
		private readonly i: $Kic
	) {
		super();

		this.g(vscode.workspace.onDidChangeConfiguration(e => {
			if (inlayHintSettingNames.some(settingName => e.affectsConfiguration(b.id + '.' + settingName))) {
				this.a.fire();
			}
		}));

		// When a JS/TS file changes, change inlay hints for all visible editors
		// since changes in one file can effect the hints the others.
		this.g(vscode.workspace.onDidChangeTextDocument(e => {
			if (b.languageIds.includes(e.document.languageId)) {
				this.a.fire();
			}
		}));
	}

	async provideInlayHints(model: vscode.TextDocument, range: vscode.Range, token: vscode.CancellationToken): Promise<vscode.InlayHint[]> {
		const filepath = this.f.toOpenTsFilePath(model);
		if (!filepath) {
			return [];
		}

		if (!areInlayHintsEnabledForFile(this.b, model)) {
			return [];
		}

		const start = model.offsetAt(range.start);
		const length = model.offsetAt(range.end) - start;

		await this.i.ensureConfigurationForDocument(model, token);

		const response = await this.f.execute('provideInlayHints', { file: filepath, start, length }, token);
		if (response.type !== 'response' || !response.success || !response.body) {
			return [];
		}

		return response.body.map(hint => {
			const result = new vscode.InlayHint(
				Position.fromLocation(hint.position),
				hint.text,
				hint.kind && fromProtocolInlayHintKind(hint.kind)
			);
			result.paddingLeft = hint.whitespaceBefore;
			result.paddingRight = hint.whitespaceAfter;
			return result;
		});
	}
}

function fromProtocolInlayHintKind(kind: Proto.InlayHintKind): vscode.InlayHintKind | undefined {
	switch (kind) {
		case 'Parameter': return vscode.InlayHintKind.Parameter;
		case 'Type': return vscode.InlayHintKind.Type;
		case 'Enum': return undefined;
		default: return undefined;
	}
}

function areInlayHintsEnabledForFile(language: LanguageDescription, document: vscode.TextDocument) {
	const config = vscode.workspace.getConfiguration(language.id, document);
	const preferences = $Mic(config);

	return preferences.includeInlayParameterNameHints === 'literals' ||
		preferences.includeInlayParameterNameHints === 'all' ||
		preferences.includeInlayEnumMemberValueHints ||
		preferences.includeInlayFunctionLikeReturnTypeHints ||
		preferences.includeInlayFunctionParameterTypeHints ||
		preferences.includeInlayPropertyDeclarationTypeHints ||
		preferences.includeInlayVariableTypeHints;
}

export function $Pjc(
	selector: DocumentSelector,
	language: LanguageDescription,
	client: ITypeScriptServiceClient,
	fileConfigurationManager: $Kic
) {
	return $Sic([
		$Tic(client, TypeScriptInlayHintsProvider.minVersion),
		$Vic(client, ClientCapability.Semantic),
	], () => {
		const provider = new TypeScriptInlayHintsProvider(language, client, fileConfigurationManager);
		return vscode.languages.registerInlayHintsProvider(selector.semantic, provider);
	});
}
