import * as vscode from 'vscode';

export const $Hhc = 'typescript';
export const $Ihc = 'typescriptreact';
export const $Jhc = 'javascript';
export const $Khc = 'javascriptreact';
export const $Lhc = 'jsx-tags';

export const $Mhc = [
	$Jhc,
	$Khc,
	$Hhc,
	$Ihc,
];

export function $Nhc(doc: vscode.TextDocument) {
	return vscode.languages.match([$Hhc, $Ihc, $Jhc, $Khc], doc) > 0;
}

export function $Ohc(doc: vscode.TextDocument) {
	return vscode.languages.match([$Hhc, $Ihc], doc) > 0;
}
