import * as fs from 'fs';
import $iic from '../logging/tracer';
import { $Akc } from '../utils/temp.electron';
import { OngoingRequestCanceller, OngoingRequestCancellerFactory } from './cancellation';

export class $Bkc implements OngoingRequestCanceller {
	public readonly cancellationPipeName: string;

	public constructor(
		private readonly a: string,
		private readonly b: $iic,
	) {
		this.cancellationPipeName = $Akc('tscancellation');
	}

	public tryCancelOngoingRequest(seq: number): boolean {
		if (!this.cancellationPipeName) {
			return false;
		}
		this.b.trace(this.a, `TypeScript Server: trying to cancel ongoing request with sequence number ${seq}`);
		try {
			fs.writeFileSync(this.cancellationPipeName + seq, '');
		} catch {
			// noop
		}
		return true;
	}
}


export const $Ckc = new class implements OngoingRequestCancellerFactory {
	create(serverId: string, tracer: $iic): OngoingRequestCanceller {
		return new $Bkc(serverId, tracer);
	}
};
